import {
	mapState,
    mapMutations
} from 'vuex';
import simpleDialog from '@/components/loading/dialog.vue'
import simpleLoading from '@/components/loading/dialog-loading.vue'
function getDate(type) {
	const date = new Date();
	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();
	if (type === 'start') {
		year = year - 60;
	} else if (type === 'end') {
		year = year + 2;
	}
	month = month > 9 ? month : '0' + month;;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
}
export default {
	computed: mapState([ 'UserInfo']),
	components:{
		simpleDialog,
		simpleLoading
	},
	data() {
		return {
			scrollLeft: 0,
			isClickChange: false,
			tabIndex: 0,
			newsitems: [
				{
					id:0,
					arrlist:[]
				},
				{
					id:1,
					arrlist:[]
				},
				{
					id:2,
					arrlist:[]
				}
			],
			sDialog: {
				message: '是否领取加油券',
				openbtn:'确定'
			},
			tabBars: [{
				name: '汇总',
				id:0
			}, {
				name: '新发券',
				id:1
			}, {
				name: '明细',
				id:2
			}],
			couponitem:'',
			array: ['绵阳我要加油电子商务有限公司', '上略互动有限公司'],
			index:1,
			indexs:1,
			arrays:['100','200'],
			Money:'0.00',
			companytext:'绵阳我要加油电子商务有限公司',
			date: getDate({
				format: true
			}),
			startDate:getDate('start'),
			endDate:getDate('end'),
			dates: getDate({
				format: true
			}),
			startDates:getDate('start'),
			endDates:getDate('end'),
			
		}
	},
	onLoad: function() {
		//this.newsitems = this.randomfn()
		// this.newsitems = this.randomfn()
	},
	methods: {
		bindDateChange: function(e) {
			this.date = e.target.value
		},
		bindDateChanges: function(e) {
			this.dates = e.target.value
		},
		goDetail(e) {
			uni.navigateTo({
				url: '/pages/template/tabbar/detail/detail?data=' + e.title
			})
		},
		bindPickerChange: function(e) {
			console.log(e)
			this.companytext = this.array[e.target.value]
		},
		bindPickerChanges:function(e) {
			console.log(e)
			this.Money = this.arrays[e.target.value]
		},
		//领取优惠券
		Receive(item){
			console.log(item)
			this.couponitem=item
			this.$refs.simpleDialog2.confirm({
				title: '提示',
				message:this.sDialog.message
			});
		},
		
		close(index1, index2) {
			uni.showModal({
				content: '是否删除本条信息？',
				success: (res) => {
					if (res.confirm) {
						this.newsitems[index1].data.splice(index2, 1);
					}
				}
			})
		},
		loadMore(e) {
			this.newsitems[e].loadingType = 1;
			setTimeout(() => {
				this.addData(e);
			}, 1200);
		},
		addData(e) {
			if (this.newsitems[e].data.length > 30) {
				this.newsitems[e].loadingType = 2;
				return;
			}
			for (let i = 1; i <= 10; i++) {
				this.newsitems[e].data.push(this['data' + Math.floor(Math.random() * 5)]);
			}
			this.newsitems[e].loadingType = 1;
		},
		async changeTab(e) {
			let index = e.detail.current;
			// console.log(index)
			if (!this.isClickChange) {
				this.tabIndex = index;
				this.isClickChange = false;
				return;
			}
		},
		
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select('#' + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			});
		},
		async tapTab(index) { //点击tab-bar
			if (this.tabIndex === index) {
				return false;
			} else {
				let tabBar = await this.getElSize("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				this.scrollLeft = tabBarScrollLeft;
				this.isClickChange = false;
				this.tabIndex = index;
			}
		},
		randomfn() {
			let ary = [];
			for (let i = 0, length = this.tabBars.length; i < length; i++) {
				let aryItem = {
					loadingType:i,
					data: []
				};
				for (let j = 1; j <= 10; j++) {
					aryItem.data.push(this['data' + Math.floor(Math.random() * 5)]);
				}
				ary.push(aryItem);
			}
			return ary;
		}
	}
}
